<template>
  <div class="desktop-landing-search">
    <img src="../../../assets/SVG/web.svg" class="search-svg" alt="search" />
    <img src="../../../assets/SVG/career.svg" class="career-svg" alt="search" />
    <h1 class="desktop-landing-search__header">Make your first career search</h1>
    <!---------------------------------------SEARCH WRAPPER--------------------------------------->
    <div class="desktop-landing-search__search-wrapper">
      <div class="desktop-landing-search__input-box">
        <label for="inputType" ref="typeLabel">Graduate or Internship</label>
        <input
          type="text"
          class="desktop-landing-search__input-box__input-search"
          id="inputType"
          placeholder="Graduate or Internship"
          ref="inputJobType"
          @input="showLabel('type')"
          v-model="inputType"
        />
      </div>
      <div class="desktop-landing-search__input-box">
        <label for="inputIndustry" ref="industryLabel">Industry</label>
        <input
          type="text"
          class="desktop-landing-search__input-box__input-search"
          id="inputIndustry"
          placeholder="Industry"
          ref="inputIndustry"
          @input="showLabel('industry')"
          v-model="inputIndustry"
        />
      </div>
      <div class="desktop-landing-search__input-box">
        <label for="inputLocation" ref="locationLabel">Location</label>
        <input
          type="text"
          class="desktop-landing-search__input-box__input-search"
          id="inputLocation"
          placeholder="Location"
          ref="inputLocation"
          @input="showLabel('location')"
          v-model="inputLocation"
        />
      </div>
      <div class="desktop-landing-search__input-box">
        <button
          class="desktop-landing-search__btn-search"
          v-bind:class="{ activebtn: inputsFilled }"
        >Search</button>
      </div>
    </div>
    <!---------------------------------------ONE CLICK SEARCH WRAPPER--------------------------------------->
    <h1 class="desktop-landing-search__header">Or choose from our recent popular job searches</h1>
    <div class="desktop-landing-search__one-click-wrapper">
      <div class="desktop-landing-search__one-click-box">
        <p class="desktop-landing-search__one-click-box__p-info disable-highlight-text">
          Graduate
          <br />Web Developer
          <br />Manchester UK
        </p>
      </div>
      <div class="desktop-landing-search__one-click-box">
        <p class="desktop-landing-search__one-click-box__p-info disable-highlight-text">
          Intern
          <br />Fashion Buyer
          <br />London UK
        </p>
      </div>
      <div class="desktop-landing-search__one-click-box">
        <p class="desktop-landing-search__one-click-box__p-info disable-highlight-text">
          Graduate
          <br />Data Engineer
          <br />Bristol UK
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DesktopLandingSearch",
  data: function() {
    return {
      inputType: "",
      inputIndustry: "",
      inputLocation: "",
      inputsFilled: false
    };
  },
  methods: {
    showLabel: function(input) {
      if (input === "type" && this.inputType !== "") {
        this.$refs.typeLabel.style.opacity = "1";
        this.$refs.typeLabel.style.top = "-150%";

        this.$refs.inputJobType.style.border = "0.3rem solid green";
        this.$refs.inputJobType.style.borderTopLeftRadius = "0";
        this.$refs.inputJobType.style.borderTopRightRadius = "0";
      } else if (input === "type" && this.inputType === "") {
        this.$refs.typeLabel.style.top = "0";
        this.$refs.typeLabel.style.opacity = "0";

        this.$refs.inputJobType.style.border = "0.3rem solid #4285f4";
        this.$refs.inputJobType.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputJobType.style.borderTopRightRadius = "2.5rem";
      } else if (input === "industry" && this.inputIndustry !== "") {
        this.$refs.industryLabel.style.opacity = "1";
        this.$refs.industryLabel.style.top = "-150%";

        this.$refs.inputIndustry.style.border = "0.3rem solid green";
        this.$refs.inputIndustry.style.borderTopLeftRadius = "0";
        this.$refs.inputIndustry.style.borderTopRightRadius = "0";
      } else if (input === "industry" && this.inputIndustry === "") {
        this.$refs.industryLabel.style.top = "0";
        this.$refs.industryLabel.style.opacity = "0";

        this.$refs.inputIndustry.style.border = "0.3rem solid #4285f4";
        this.$refs.inputIndustry.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputIndustry.style.borderTopRightRadius = "2.5rem";
      } else if (input === "location" && this.inputLocation !== "") {
        this.$refs.locationLabel.style.opacity = "1";
        this.$refs.locationLabel.style.top = "-150%";

        this.$refs.inputLocation.style.border = "0.3rem solid green";
        this.$refs.inputLocation.style.borderTopLeftRadius = "0";
        this.$refs.inputLocation.style.borderTopRightRadius = "0";
      } else if (input === "location" && this.inputLocation === "") {
        this.$refs.locationLabel.style.top = "0";
        this.$refs.locationLabel.style.opacity = "0";

        this.$refs.inputLocation.style.border = "0.3rem solid #4285f4";
        this.$refs.inputLocation.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputLocation.style.borderTopRightRadius = "2.5rem";
      } else {
        null;
      }

      if (
        this.inputType !== "" &&
        this.inputIndustry !== "" &&
        this.inputLocation !== ""
      ) {
        this.inputsFilled = true;
      } else {
        this.inputsFilled = false;
      }
    }
  }
};
</script>

<style lang="scss">
@import "../../../styles/_variables.scss";
.desktop-landing-search {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 5vh;
  &__header {
    font-size: 4rem;
    padding: 4rem;
    border-radius: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vh;
    color: $colour4;
    z-index: 10;
    margin-bottom: 2rem;
    background-color: $colour1;
    text-align: center;
  }
  &__search-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 30%;
    z-index: 10;
  }
  &__one-click-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  &__input-box {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-right: 2.5%;
    justify-content: center;
    height: 4rem;
    & label {
      width: 36rem;
      font-size: 2rem;
      padding: 1rem 2rem;
      margin-bottom: 1rem;
      position: absolute;
      opacity: 0;
      transition: all 0.5s;
      color: $colour4;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      background-color: $colour1;
    }
    &__input-search {
      height: 6rem;
      width: 36rem;
      border: none;
      border-radius: 2.5rem;
      padding: 2rem;
      font-size: 2rem;
      border: 0.3rem solid $colour1;
      transition: all 0.5s;
      &:focus {
        border: 0.3rem solid $colour-grey !important;
      }
    }
  }
  &__one-click-box {
    cursor: pointer;
    height: 12rem;
    background-color: $colour1;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2.5% 1%;
    padding: 4rem;
    border: 0.2rem solid transparent;
    transition: all 0.2s;
    &__p-info {
      font-size: 2rem;
      color: $colour4;
      text-align: center;
    }
    &:hover {
      background-color: transparent;
      border: 0.2rem solid $colour1;
      -webkit-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      -moz-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      transform: translateY(-1rem);
    }
    &:active {
      background-color: transparent;
      border: 0.2rem solid $colour1;
      -webkit-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      -moz-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      transform: translateY(0);
    }
    &:hover p {
      color: $colour1;
    }
  }
  &__btn-search {
    width: 15rem;
    height: 6rem;
    font-size: 2rem;
    background-color: $colour-grey;
    border-radius: 2.5rem;
    padding: 1rem 2rem;
    color: $colour4;
    border: solid 0.3rem transparent;
    transition: all 0.2s;
    pointer-events: none;
  }
}

.activebtn {
  background-color: $colour1;
  color: $colour4;
  border: solid 0.3rem transparent;
  cursor: pointer;
  pointer-events: all;
  &:hover {
    background-color: transparent;
    border: solid 0.3rem $colour1;
    color: $colour1;
  }
}

.search-svg {
  position: absolute;
  width: 25%;
  right: 5%;
  bottom: 5%;
  opacity: 0.3;
  pointer-events: none;
}

.career-svg {
  position: absolute;
  width: 50%;
  left: 2.5%;
  top: 20%;
  opacity: 0.3;
  pointer-events: none;
}
</style>
